import './App.css';
import Tabs from './Tabs'
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core/styles';
import React from 'react';



const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#757ce8',
      main: '#3f50b5',
      dark: '#002884',
      contrastText: '#fff',
    },
    secondary: {
      light: '#ff7961',
      main: '#f44336',
      dark: '#ba000d',
      contrastText: '#000',
    },
  },
});


function App() {
  return (

    <MuiThemeProvider theme={theme}>

    <Tabs /> 
    </MuiThemeProvider >

  );
}

export default App;
