import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
    table: {
        minWidth: 650,
    },
});



export default function BasicTable(props) {

    const classes = useStyles();
    const { rows } = props;

    return (
        <TableContainer component={Paper}>
            <Table className={classes.table} aria-label="simple table">
                <TableHead>
                    <TableRow>
                        <TableCell align="center">Nombre</TableCell>
                        <TableCell align="center">SocketID</TableCell>
                        <TableCell align="center">Puerto Aplicaci√≥n</TableCell>
                        <TableCell align="center">Uso CPU</TableCell>
                        <TableCell align="center">Memoria Usada</TableCell>
                        <TableCell align="center">Memoria Libre</TableCell>
                        <TableCell align="center">Conexiones Actuales</TableCell>
                        <TableCell align="center">Tiempo de respuesta monitoreo</TableCell>
                        <TableCell align="center">Up-Time</TableCell>
                    </TableRow>
                </TableHead>
                <TableBody>
                    {rows.map((row, index) => (
                        <TableRow key={index}>
                            <TableCell align="center">{row.Nombre}</TableCell>
                            <TableCell align="center">{row.SocketID}</TableCell>
                            <TableCell align="center">{row.Port}</TableCell>
                            <TableCell align="center">{row.CPUUsage}</TableCell>
                            <TableCell align="center">{row.MemUsed}</TableCell>
                            <TableCell align="center">{row.MemFree}</TableCell>
                            <TableCell align="center">{row.Conexiones}</TableCell>
                            <TableCell align="center">{row.Time}ms</TableCell>
                            <TableCell align="center">{row.Uptime}hr</TableCell>
                        </TableRow>
                    ))}
                </TableBody>
            </Table>
        </TableContainer>
    );
}