import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tables from './Tables'
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';
import axios from 'axios'

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box p={3}>
                    {children}
                </Box>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        backgroundColor: theme.palette.background.paper,
    },
}));

export default function SimpleTabs() {
    const classes = useStyles();
    const [value, setValue] = useState(0);
    const [serverList, setserverList] = useState([]);
    const [loadsList, setloadsList] = useState([]);
    const [masterList, setmasterList] = useState([]);

    const llamados = async () => {


        try {
            const valueGets = await axios.get('/status');
            setserverList(valueGets.data.filter(element => element.nombre.includes('Server')));
            setloadsList(valueGets.data.filter(element => element.nombre.includes('Loads')));
            setmasterList(valueGets.data.filter(element => element.nombre.includes('Master')));

        }
        catch (error) {
            console.log(error);
        }

    }

    useEffect(() => {
        setInterval(llamados, 1000)
    }, [])

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div className={classes.root}>
            <AppBar position="static">
                <Tabs value={value} onChange={handleChange} aria-label="simple tabs example">
                    <Tab label="Servers" {...a11yProps(0)} />
                    <Tab label="Masters" {...a11yProps(1)} />
                    <Tab label="Load Balancers" {...a11yProps(2)} />
                </Tabs>
            </AppBar>
            <TabPanel value={value} index={0}>
                <Tables rows={serverList.map(element => element.value)} />
            </TabPanel>
            <TabPanel value={value} index={1}>
                <Tables rows={masterList.map(element => element.value)} />
            </TabPanel>
            <TabPanel value={value} index={2}>
                <Tables rows={loadsList.map(element => element.value)} />
            </TabPanel>
        </div>
    );
}